import * as urls from './utils.js'
import axios from '../config/http'

// 接口封装
function request (url = '', data = {}, type = 'GET', config) {
  type = type.toLowerCase()
  axios.defaults.baseURL = 'http://' + location.host
  return axios[type](url, data, config).then(function (response) {
    return response.data
  }).catch(function (e) {
    return e
  })
}



// ---------------登录------------------
export const login = (data) => request(urls.login.render(), data, 'POST',  {type: 'json'})
// 添加学生
export const addStudent = (data) => request(urls.addStudent, data, 'POST', {type: 'json'})
// 修改学生
export const upadteStudent = (id, data) => request(urls.upadteStudent.render({id}), data, 'POST', {type: 'json'})
// 查询学生
export const queryStudent = (data) => request(urls.queryStudent, data, 'POST', {type: 'json'})
// 删除学生
export const deleteStudent = (id) => request(urls.deleteStudent.render({id}), {}, 'GET')
